import React from "react"
import { formatDistance } from "date-fns"
import { cs } from "date-fns/locale"
import { DEADLINE } from "../constants"

const Popup = () => {
  const now = new Date()

  const getPopupMessage = () => {
    if (now >= DEADLINE) {
      return "Pihlaovn do soute je ukoneno! Dkujeme vem za ast."
    }

    const oneMonthBeforeDeadline = new Date(DEADLINE)
    oneMonthBeforeDeadline.setMonth(DEADLINE.getMonth() - 1)

    if (now >= oneMonthBeforeDeadline) {
      return `Pihlaovn do soute skon u ${formatDistance(
        DEADLINE,
        now,
        {
          addSuffix: true,
          locale: cs,
        }
      )}!`
    }
    const longDateString = new Intl.DateTimeFormat("cs-CZ", {
      day: "numeric",
      month: "long",
      year: "numeric",
    }).format(DEADLINE)
    return `Pihlaovn do soute skon u ${longDateString}.`
  }

  return <div className="popup">{getPopupMessage()}</div>
}

export default Popup
